import styled from 'styled-components';

export const StyledLoadMoreBtn = styled.div`
    margin: auto;
    text-align: center;
    button {
        transition: 0.1s;
        font-family: 'Abel', sans-serif;
        font-size: 24px;
        margin: 15px 0;
        height: 50px;
        width: 250px;
        border-radius: 25px;
        background-color: black;
        color: #fff;
        border: none;
        text-align: center;
    }
    button:hover {
        opacity: 0.9;
        cursor: pointer;
    }
    button:active {
        opacity:0.9;
        transition: 0.1s;
        border-inline: 2px solid black;
        background-color: rgb(44, 44, 44);
    }
`